// 长字符段申略处理
const stringtomore = (str, length = 15) => {
  if (str.length <= length) {
    return str;
  }
  const result = str.substring(0, length - 2) + "...";
  return result;
};

// 数字转字符串
const numbertostring = num => {
  return ("0" + num).slice(-2);
};

const isMobile = () => {
  if (
    navigator.userAgent.match(/Android/i) ||
    navigator.userAgent.match(/webOS/i) ||
    navigator.userAgent.match(/iPhone/i) ||
    navigator.userAgent.match(/iPad/i) ||
    navigator.userAgent.match(/iPod/i) ||
    navigator.userAgent.match(/BlackBerry/i) ||
    navigator.userAgent.match(/Windows Phone/i)
  ) {
    return true;
  } else {
    return false;
  }
};
// 返回日期时间格式
export const formatDateChinese = value => {
  const date = new Date(value);
  const y = date.getFullYear();
  let m = date.getMonth() + 1;
  m = m < 10 ? "0" + m : m;
  let d = date.getDate();
  d = d < 10 ? "0" + d : d;
  let hour = date.getHours();
  hour = hour < 10 ? "0" + hour : hour;
  let minute = date.getMinutes();
  let second = date.getSeconds();
  minute = minute < 10 ? "0" + minute : minute;
  second = second < 10 ? "0" + second : second;
  return y + "年" + m + "月" + d + "日 " + hour + ":" + minute + ":" + second;
};

export const tagToPureString = str => {
  let div = document.createElement("div");
  div.innerHTML = str;
  let text = div.textContent || div.innerText || "";
  return text;
};
export { stringtomore, numbertostring, isMobile };
