import React, { Component } from "react";
import ReactDOM from "react-dom";

import {
  List,
  NavBar,
  Icon,
  Switch,
  Toast,
  PullToRefresh,
  ListView,
  Button
} from "antd-mobile";
import { setStore, getStore, removeStore } from "util/store";
import { StickyContainer, Sticky } from "react-sticky";

const Item = List.Item;
const Brief = Item.Brief;

const imageHost = "https://jiguangdanci.easylesson.cn";
import { setValue } from "@/redux/teacher-question";
import { connect } from "react-redux";
import { get } from "util/fetch";
import { tagToPureString } from "util/tool";
import "./index.less";
const colorTheme = {
  0: "#e84118",
  1: "#00a8ff",
  2: "#fbc531",
  3: "#4cd137",
  4: "#487eb0",
  5: "#9c88ff"
};
const getUrlVars = url => {
  var vars = {};
  var parts = url.replace(/[?&]+([^=&]+)=([^&]*)/gi, function(m, key, value) {
    vars[key] = value;
  });
  return vars;
};

const routePath = [
  "/api/exam_boot/teacher-collection",
  "/api/exam_boot/teacher-collection-question"
];

class index extends Component {
  state = {
    dataBlob: {},
    dataSource: new ListView.DataSource({
      rowHasChanged: (r1, r2) => r1 !== r2,
      sectionHeaderHasChanged: (s1, s2) => s1 !== s2
    }),

    collection_id: "",
    collection: {},
    total: 0,
    collection_questions: [],
    isTrain: false,
    height: document.documentElement.clientHeight,
    refreshing: true,
    isLoading: false,
    pageSize: 5,
    pageNo: 1,
    isEnd: 0,
    user_id: ""
  };
  componentDidMount() {
    const hei = this.state.height - ReactDOM.findDOMNode(this.lv).offsetTop;

    this.setState({
      height: hei
    });
    this.rData = [];
    this.init();
  }
  init = () => {
    let query = this.props.location.search;
    let collection_id = getUrlVars(query)["collection_id"];
    let token = getUrlVars(query)["token"];
    if (token === undefined) {
      Toast.info("授权失效", 2);
      return;
    }
    setStore("token", token);
    let user_id = getUrlVars(query)["user_id"];
    this.setState({
      user_id
    });
    this.state.collection_id = collection_id;
    get(`${routePath[0]}/${collection_id}`).then(res => {
      this.setState({
        collection: res.data[0]
      });
    });
    this.fetchWords();
  };
  fetchWords = () => {
    if (this.state.isLoading) {
      return;
    }
    this.setState({
      isLoading: true
    });
    let params = {
      collection_id: this.state.collection_id,
      equal: 1,
      pageSize: this.state.pageSize,
      pageNo: this.state.pageNo
    };
    get(`${routePath[1]}`, params)
      .then(res => {
        let tempList = res.data.map(item => {
          item.content = tagToPureString(item.content);
          item.analysis = tagToPureString(item.analysis);
          return item;
        });
        let collection_questions = [
          ...this.state.collection_questions,
          ...tempList
        ];
        this.rData = res.data.map((item, index) => {
          return index + (this.state.pageNo - 1) * this.state.pageSize;
        });
        var tempDataBlob = this.state.dataBlob;
        tempDataBlob[`${this.state.pageNo}`] = this.rData;

        this.setState({
          dataBlob: tempDataBlob,
          dataSource: this.state.dataSource.cloneWithRowsAndSections(
            tempDataBlob
          ),
          collection_questions,
          total: res.count,
          pageNo: parseInt(res.pageNo) + 1,
          isLoading: false
        });
      })
      .catch(err => {
        Toast.info("认证失效", 2);
      });
  };

  handleClick = id => {
    const { isTrain } = this.state;
    if (isTrain) {
      this.props.history.push(`/train-word?collection_id=${id}`);
    } else {
      this.props.history.push(`/learn-word?collection_id=${id}`);
    }
  };
  gotoLast = () => {
    this.props.history.go(-1);
  };
  onEndReached = e => {
    let { total, pageNo, pageSize } = this.state;
    if (pageNo * pageSize - pageSize >= total) {
      this.setState({
        isEnd: 1
      });
      return;
    }
    this.fetchWords();
  };
  onScroll = e => {
    if (
      this.lv.listviewRef.scrollProperties.offset +
        this.lv.listviewRef.scrollProperties.visibleLength >=
      this.lv.listviewRef.scrollProperties.contentLength
    ) {
      this.onEndReached();
    }
  };
  phoneticSound = (item, e) => {
    let phonetic_sound = new Audio(imageHost + "/" + item.pronunciation_url);
    phonetic_sound.play();
    e.stopPropagation();
  };

  syncReduxData = async route => {
    let params = {
      collection_id: this.state.collection_id,
      equal: 1,
      pageSize: parseInt(this.state.total),
      // pageSize: 2,
      pageNo: 1
    };
    let temp = await get(`${routePath[1]}`, params);
    let tempList = temp.data.map(item => {
      // item.content = tagToPureString(item.content);
      // item.analysis = tagToPureString(item.analysis);
      return item;
    });
    let value = {
      list: tempList,
      collection_info: this.state.collection,
      user_id: this.state.user_id,
      exam_address: window.location.href
    };
    this.props.setValue(value);
    this.props.history.push(route);
  };
  lookExam = () => {
    this.syncReduxData("/look-exam");
  };
  takeExam = () => {
    this.syncReduxData("/take-exam");
  };
  render() {
    const navBar = (
      <NavBar
        mode="light"
        onLeftClick={this.gotoLast}
        leftContent={<Icon type="left" />}
        rightContent={[<Icon key="1" type="ellipsis" />]}
      >
        Come on
      </NavBar>
    );
    const header = (
      <div className="header-container">
        <h2>教师资格考试</h2>
        <div className="header">
          <div className="title">
            {this.state.collection.name}
            <p>
              {this.state.collection.note} <span>[{this.state.total}道题]</span>
            </p>
          </div>
        </div>
        <div className="entry">
          <div>
            <div>练习入口</div>
            <div className="btn-container">
              <div>
                <Button
                  onClick={this.takeExam}
                  style={{ background: " RGB(101, 209, 57)" }}
                  type="primary"
                >
                  练习
                </Button>
              </div>
              <div>
                <Button
                  onClick={this.lookExam}
                  className="remember"
                  type="primary"
                >
                  背题
                </Button>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
    const list = (
      <div className="list">
        {this.state.collection_questions.map(item => (
          <div
            className="cell"
            key={JSON.stringify(item)}
            onClick={() => this.handleClick(item.id)}
          >
            <div>
              <p>
                {item.word}{" "}
                <span className="grey">[{item.phonetic_symbol}]</span>{" "}
              </p>
              <p className="grey">{item.definition}</p>
            </div>
          </div>
        ))}
      </div>
    );

    const row = (rowData, sectionID, rowID) => {
      return (
        <div className="cell">
          <div>
            <p className="title">第 {rowData + 1} 题 </p>
            <p>{this.state.collection_questions[parseInt(rowData)].content} </p>
          </div>
        </div>
      );
    };
    const row1 = rowData => <div>{rowData}</div>;

    const listview = (
      <ListView
        useBodyScroll
        renderSectionWrapper={sectionID => (
          <StickyContainer
            key={`s_${sectionID}_c`}
            className="sticky-container"
            style={{ zIndex: 4 }}
          />
        )}
        renderSectionHeader={(sectionData, sectionID) => {
          return (
            <Sticky>
              {({ style }) => (
                <div
                  className="sticky"
                  style={{
                    ...style,
                    zIndex: 3,
                    backgroundColor: colorTheme[parseInt(sectionID) % 5],
                    color: "white"
                  }}
                >
                  第 {sectionID} 组
                </div>
              )}
            </Sticky>
          );
        }}
        className="list"
        ref={el => (this.lv = el)}
        dataSource={this.state.dataSource}
        style={{ height: this.state.height }}
        renderRow={row}
        pageSize={5}
        initialListSize={10}
        onEndReached={this.onEndReached}
        onEndReachedThreshold={500}
        onScroll={this.onScroll}
        renderFooter={() => (
          <div style={{ lineHeight: "30px", textAlign: "center" }}>
            {this.state.isLoading
              ? "Loading..."
              : !this.state.isEnd
              ? "已加载"
              : "到达底部"}
          </div>
        )}
      />
    );
    return (
      <div className="share-word-container">
        {navBar}
        {header}
        {listview}
      </div>
    );
  }
}

const mapStateToProps = state => ({
  redux: state.teacherQuestion
});
const mapDispatchToProps = dispatch => {
  return {
    setValue: value => {
      dispatch(setValue(value));
    }
  };
};
export default connect(
  mapStateToProps,
  mapDispatchToProps
)(index);
